import { dbService, storagesService } from "fbase";
import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPencilAlt } from "@fortawesome/free-solid-svg-icons";

const Nweet = ({ nweetObj, isOwner }) => {
    const [editing, setEditing] = useState(false);
    const [newNweet, setNewNweet] = useState(nweetObj.text);
    const onDeleteClick = async () => {
        const ok = window.confirm("Are you sure you want to delee this nweet?");
        console.log(ok);
        if (ok) {
            //delete nweets
            await dbService.doc(`nweets/${nweetObj.id}`).delete();
            await storagesService.refFromURL(nweetObj.attachmentUrl).delete();
        }
    };
    const toggleEditing = () => setEditing(prev => !prev);   
    const onSubmit = async (event) => {
        event.preventDefault();
        await dbService.doc(`nweets/${nweetObj.id}`).update({
            text: newNweet
        });
        setEditing(false);
    };
    const onChange = (event) => {
        const {
            target: { value },
        } = event;
        setNewNweet(value);
    };
return (
    <div className="nweet">
        {
            editing ? (
                <>
                    <form onSubmit={onSubmit}
                    className="container nweetEdit">
                        <input 
                            type="text"
                            placeholder="Edit you nweet" 
                            value={newNweet} 
                            required 
                            onChange={onChange}
                         />
                         <input type="submit" value="Update Nweet" className="formBtn" />
                    </form> 
                    <span onClick={toggleEditing} className="formBtn cancelBtn">
            Cancel
          </span>
                    <button onClick={toggleEditing}>Cancel</button>
                </>
            ) : (
                <>
                    <h4>{nweetObj.text}</h4>
                    {nweetObj.attachmentUrl && (
                        <img src={nweetObj.attachmentUrl} width="50px" height="50px" />)
                    }
                    {isOwner && (
                        <div class="nweet__actions">
                            <span onClick={onDeleteClick}>
                            <FontAwesomeIcon icon={faTrash} />
                            </span>
                            <span onClick={toggleEditing}>
                            <FontAwesomeIcon icon={faPencilAlt} />
                        </span>
                        </div>
                    )}
                    </>
            )}
    </div>
)
};

export default Nweet;